* {
  transition: all 0.3s;
}

/* Default Scroll Hide  */
html {
  scrollbar-color: #666 #1a202c !important;
  scrollbar-width: thin !important;
}

::-webkit-scrollbar {
  width: 8px !important;
}

::-webkit-scrollbar-track {
  background: #1a202c !important;
}

::-webkit-scrollbar-thumb {
  background: #666 !important;
  border-radius: 10px;
}
/* Default Scroll Hide  */

body {
  background: #1a202c;
  color: #ddd;
}

.navOpen {
  transform: translateX(-0%);
  transition: all 0.5s;
}

.navHide {
  transform: translateX(-105%);
  transition: all 2s;
}

.slide_hide {
  opacity: 0;
  transition: all 1s;
}
.slide_show {
  opacity: 1;
  transition: all 1s;
}

#smallSlide {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  grid-gap: 5px;
}

.shadow-custom {
  box-shadow: 0px 0px 3px 1px #666;
}

@media (max-width: 767px) {
  .md-none {
    display: none !important;
  }
}
